	import uniPopup from '@/components/uni-popup/uni-popup.vue'
    import uniIcon from "@/components/uni-icon/uni-icon.vue"
	import cmdIcon from "@/components/cmd-icon/cmd-icon.vue"
	import wlpNumberShopping from "../../components/wlp-number-shopping/wlp-number-shopping.vue"
	import calendar from '../../components/date-picker/date-picker.vue'
	export default {
		components: {
			uniPopup,
			uniIcon,
			cmdIcon,
			wlpNumberShopping,
			calendar
		},
		props:{
			showHeader1:{
				type:Boolean,
				default:false
			},
			showHeader2:{
				type:Boolean,
				default:false
			},
			showDate1:{
				type:Boolean,
				default:false
			},
			showDate2:{
				type:Boolean,
				default:false
			},
			showDate3:{
				type:Boolean,
				default:false
			},
			showDate4:{
				type:Boolean,
				default:false
			},
			showMessage1:{
				type:Boolean,
				default:false
			},
			showMessage2:{
				type:Boolean,
				default:false
			},
			showMessage3:{
				type:Boolean,
				default:false
			},
			showMessage4:{
				type:Boolean,
				default:false
			},
			showRule1:{
				type:Boolean,
				default:false
			},
			showRule2:{
				type:Boolean,
				default:false
			},
			showBottom1:{
				type:Boolean,
				default:false
			},
			showBottom2:{
				type:Boolean,
				default:false
			},
			url:{
				type:String,
				default:""
			},
			dateLeft:{
				type:String,
				default:""
			},
			dateMid:{
				type:String,
				default:""
			},
			messageContent:{
				type:String,
				default:""
			},
			goodsMag:{
				type:String,
				default:"请在出行的前一日18：00前预定"
			}
		},
		data() {
			return {
				goodsName:"桃花源禅意民宿商户",
				goodsPicture:"/static/1.6.jpg",
				goodsType:"单人间",
				goodsPrice:180,
				phoneNumber:"15029092646",
				moneys:"￥1080",
				type: '',
				time:'18:00',
				beginDate: '',
				beginDateWeek:'',
				endDate: '',
				endDateWeek:'',
				showCaledar: false,
				dateSum:0,
			    Poplist:[
			    	"14:00","15:00","16:00","17:00",
			    	"18:00","19:00","20:00","21:00",
			    	"22:00","23:00","24:00","次日01:00",
			    	"次日02:00","次日03:00","次日04:00","次日05:00",
			    	"次日06:00"
			    ],
				Popdetail:[
					{
						money:"2*180",
						date:"2019.07.20"
					},
					{
						money:"2*180",
						date:"2019.07.21"
					},
					{
						money:"2*180",
						date:"2019.07.22"
					}
				]
			}
		},
		onBackPress() {
			if (this.showCaledar !== false) {
				this.showCaledar = false;
				return true;
			}
		},
		methods: {
			togglePopup(type, open) {
				this.type = type
				this.$refs[open].open()
			},
			cancel(type) {
				this.$refs[type].close()
			},
			change(e) {
				console.log(e.show)
			},
			changeTime(e){
				this.time=e.currentTarget.dataset.date;
			},
			changeNumber(e){
				this.moneys=e*this.goodsPrice*this.dateSum;
				
				for (var i = 0; i < this.Popdetail.length; i++) {
					this.Popdetail[i].money=e+"*"+this.goodsPrice;
				}
			},
			submit(){
				uni.navigateTo({
					url:this.url,
					success:function(){
						console.log("跳转成功")
					}
				})
			},
		    changeDate({ choiceDate, dayCount }) {
		    	this.beginDate = choiceDate[0].re;
		    	this.endDate = choiceDate[1].re;
		    	this.beginDateWeek = choiceDate[0].week;
		    	this.endDateWeek =  choiceDate[1].week ;
		    	this.dateSum = dayCount;
		    }
		}
	}
